import React, { Component } from 'react';

import Balance from "./Balance";
import Chart from "./Chart";
import Currency from "./Currency";
import MyAccount from "./MyAccount";
import StatsBar from "./StatsBar";

import { 
  Grid, Row, Col, Image 
} from "react-bootstrap";

import { connect } from "react-redux";
import * as actions from "../../actions";


export class Main extends Component{
  render() {
    return (
      <div className={this.props.sidebar ? "main" : "main open"}>
        <Col 
          md={this.props.sidebar ? 11 : 12} 
          smOffset={this.props.sidebar ? 1 : 0}
          >
          <Row>
          <Col md={4}><Balance /></Col>
          <Col md={8}><MyAccount /></Col>
          </Row>
          <Row>
          <Col md={12}><Chart /></Col>
          </Row>
          <StatsBar />
        </Col>
      </div>
    )
  }
}


const stateProps = (state) => ({
  sidebar : state.sidebarReducer.toggle_sidebar
});


export default connect(stateProps, actions)(Main);


