import React, { Component } from 'react';
import FontAwesome from "react-fontawesome";
import { connect } from "react-redux";
import * as actions from "../actions";

import { 
  Grid, Row, Col, Image 
} from "react-bootstrap";

class Header extends Component {
  handleSidebarToggle() {
    console.log("toggling", this.props);
    this.props.toggleSidebar(!this.props.sidebar);
  }
  render() {
    return (
      <div className="header">
        <Col 
          md={10} 
          smOffset={this.props.sidebar ? 2 : 0} 
          mdOffset={this.props.sidebar ? 1 : 0}
          >
          <div className="eq_height">
            <FontAwesome 
              name="arrow-left" 
              className="arrow toggle_sidebar"
              onClick={() => this.handleSidebarToggle() }
              />
            <span className="page_heading">Accounts overview</span>
         
            <div className="page_style navbar-right">
              <span className="page_heading_small">Page style</span>
              <FontAwesome name="th-large active" />
              <FontAwesome name="th" />
            </div>
          </div>
        </Col>
      </div>
    )
  }
}

const stateProps = (state) => ({
  sidebar : state.sidebarReducer.toggle_sidebar
});


export default connect(stateProps, actions)(Header);


