import React, { Component } from 'react';
import FontAwesome from "react-fontawesome";

import { 
  Grid, Row, Col, Image, Button
} from "react-bootstrap";


export default class Balance extends Component {
  render() {
    return (
      <div className="balance_bar">

          <h2 className="extrabold">Balance</h2>
      <Row className="balance_row flex">
      <Col md={6}>
        <h2>ETH 107.89</h2>
        <span>$.3658945</span><span>56%</span>
      </Col>
  
      <Col md={6}>
        <Button className="white_btn">
        <FontAwesome name="long-arrow-up"/><br/>
        Sell
        </Button>

      <Button className="white_btn">
        <FontAwesome name="long-arrow-down"/><br/>
        Buy
        </Button>

        <Button className="white_btn">
        <FontAwesome name="wpforms"/><br/>
        Order
        </Button>
      </Col>
      </Row>

      <Row className="balance_row">
        <Col md={6}>
          <p>Your today revenue</p>
          <h2>+$ 195</h2>
          <span>-------12%</span>
        </Col>
        <Col md={6}>
          <p>Your today revenue</p>
          <h2>+$ 195</h2>
          <span>-------12%</span>
        </Col>
      </Row>

      </div>
    )
  }
}



