import React, { Component } from 'react';
import FontAwesome from "react-fontawesome";
import { BrowserRouter as Router, Route, NavLink } from "react-router-dom";

import { connect } from "react-redux";
import * as actions from "../actions";

import { 
  Grid, Row, Col, Image 
} from "react-bootstrap";

class Sidebar extends Component {

  render() {
    return (
      <div 
        ref={(input) => this.comp = input}
        className={this.props.sidebar === true ? "sidebar vh100 open" : "sidebar vh100 close"}
        >
        <div className="sidebar_content">
        <Router>
          <ul>
            <li className="img_profile">
            <Image className="img-responsive " src="https://www.communitylandtrust.ca/wp-content/uploads/2015/10/placeholder.png" />
            </li>

            <li className="blank_bar">

            </li>
            
            <li>
              <NavLink to="/home" activeClassName="navbar_active">
              <FontAwesome name="tachometer" />
              </NavLink>
            </li>

            <li>
            <NavLink to="/one" activeClassName="navbar_active">
            <FontAwesome name="retweet" />
            </NavLink>
           
            </li>
            <li>
            <NavLink to="/" activeClassName="navbar_active">
            <FontAwesome name="credit-card" />
            </NavLink>
            </li>

            <li>
            <NavLink to="/three" activeClassName="navbar_active">
            <FontAwesome name="address-book-o" />
            </NavLink>
            </li>

            <li>
            <NavLink to="/four" activeClassName="navbar_active">
            <FontAwesome name="cog" />
            </NavLink>
            </li>

            <li className="last">
            <NavLink to="/five" activeClassName="navbar_active">
            <FontAwesome name="share-square-o" />
            </NavLink>
            </li>

          </ul>
          </Router>
        </div>
      </div>
    )
  }
}



const stateProps = (state) => ({
  sidebar : state.sidebar.toggle_sidebar
});


export default connect(stateProps, actions)(Sidebar);


