import React, { Component } from 'react';
import { findDomNode } from "react-dom";
import FontAwesome from "react-fontawesome";
import { 
  Grid, Row, Col, Image, Button
} from "react-bootstrap";

export default class StatsBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hidden : true
    }
  }
  handleToggle() {
    this.setState({
      hidden : !this.state.hidden
    });
    console.log(this.statsBarBtn);
  }
  render() {
    return (
      <div className="stats_bar">
        <span 
          className="stats_bar_btn"
          ref={(input) => this.statsBarBtn = input} 
          onClick={() => this.handleToggle()}>
          <FontAwesome name="area-chart"/>
        </span>
        {!this.state.hidden ? (<div className="stats_bar_data" >
        <ul>
        <li>
          <span className="stats_bar_data_currency">ETH/USD</span>
          <h4 className="stats_bar_data_price">300.22</h4><span className="stats_bar_data_stack">-452%</span>
        </li>

        <li>
          <span className="stats_bar_data_currency">ETH/USD</span>
          <h4 className="stats_bar_data_price">300.22</h4><span className="stats_bar_data_stack">-452%</span>
        </li>

        <li>
          <span className="stats_bar_data_currency">ETH/USD</span>
          <h4 className="stats_bar_data_price">300.22</h4><span className="stats_bar_data_stack">-452%</span>
        </li>

        <li>
          <span className="stats_bar_data_currency">ETH/USD</span>
          <h4 className="stats_bar_data_price">300.22</h4><span className="stats_bar_data_stack">-452%</span>
        </li>
        </ul>
      </div>) : ""}
      </div>
    )
  }
}



